video {
  object-fit: cover; /* Ajuste la vidéo pour couvrir la totalité du conteneur sans déformer */
}


/* Media Query pour les écrans de 700 pixels de largeur et moins */
/* Media Query pour les écrans de 500 pixels de largeur ou moins */
@media screen and (max-width: 500px) {
  video {
      width: 100%; /* La vidéo prend 100% de la largeur de son conteneur parent */
      max-width: 500px; /* Mais ne dépasse pas 500 pixels */
  }
}

/* Media Query pour les écrans entre 501 pixels et 700 pixels de largeur */
@media screen and (min-width: 501px) and (max-width: 900px) {
  video {
      width: 100%; /* La vidéo prend 100% de la largeur de son conteneur parent */
      max-width: 700px; /* Mais ne dépasse pas 700 pixels */
  }
}

/* Media Query pour les écrans de plus de 700 pixels de largeur */
@media screen and (min-width: 801px) {
  video {
      width: 700px; /* La vidéo a une largeur fixe de 700 pixels */
  }
}
